import React, {useState} from "react";
import {useNavigate} from "react-router-dom";
import {useCart} from "../../hooks/useCart";
import {CheckoutService} from "../../../core/services/CheckoutService";
import type {PaymentMethod} from "../../../core/services/CheckoutService";
import {NotificationType} from "../../contexts/CartContext";
import {extractErrorMessage} from "../../../utils/errorHandler";

interface TestCheckoutButtonProps {}

const TestCheckoutButton: React.FC<TestCheckoutButtonProps> = () => {
	const navigate = useNavigate();
	const {cart, clearCart, showNotification, appliedDiscount} = useCart();
	const checkoutService = new CheckoutService();
	const [isLoading, setIsLoading] = useState(false);

	const handleTestCheckout = async () => {
		console.log("üß™ TestCheckoutButton.handleTestCheckout INICIADO");

		if (!cart || cart.items.length === 0) {
			console.log("‚ùå Carrito vac√≠o, abortando checkout");
			showNotification(NotificationType.ERROR, "El carrito est√° vac√≠o");
			return;
		}

		console.log("üõí AN√ÅLISIS COMPLETO DEL CARRITO ANTES DEL CHECKOUT:");
		console.log("üìä Cart completo:", JSON.stringify(cart, null, 2));
		console.log("üìä Total de items en carrito:", cart.items.length);
		console.log("üìä Total del carrito:", cart.total);

		// ‚úÖ NUEVO: An√°lisis detallado de cada item
		console.log("üîç AN√ÅLISIS ITEM POR ITEM:");
		cart.items.forEach((item, index) => {
			console.log(`üìã Item ${index + 1}:`, {
				id: item.id,
				productId: item.productId,
				quantity: item.quantity,
				price: item.price,
				product: item.product ? {
					id: item.product.id,
					name: item.product.name,
					price: item.product.price,
					sellerId: item.product.sellerId,
					seller_id: item.product.seller_id,
					user_id: item.product.user_id
				} : null,
				completeItem: item
			});
		});

		// ‚úÖ NUEVO: Detectar duplicados en el carrito
		console.log("üîç VERIFICANDO DUPLICADOS EN EL CARRITO:");
		const itemsByProductId = cart.items.reduce((acc: any, item, index) => {
			if (!acc[item.productId]) {
				acc[item.productId] = [];
			}
			acc[item.productId].push({index, item});
			return acc;
		}, {});

		console.log("üìä Items agrupados por productId:", itemsByProductId);

		Object.keys(itemsByProductId).forEach(productId => {
			const items = itemsByProductId[productId];
			if (items.length > 1) {
				console.warn(`‚ö†Ô∏è DUPLICADO EN CARRITO DETECTADO para productId ${productId}:`);
				console.warn(`‚ùå Se encontraron ${items.length} items para el mismo producto`);
				items.forEach((itemData: any, i: number) => {
					console.warn(`   ${i + 1}. Item[${itemData.index}]:`, itemData.item);
				});
			} else {
				console.log(`‚úÖ Producto ${productId}: Sin duplicados (${items[0].item.quantity} unidades)`);
			}
		});

		setIsLoading(true);
		try {
			// ‚úÖ OBTENER SELLER_ID DEL CARRITO
			const sellerId = CheckoutService.getSellerIdFromCart(cart);
			console.log("üè™ Seller ID obtenido para test:", sellerId);

			// ‚úÖ CORREGIDO: Construir items del carrito con precios v√°lidos
			const items = cart.items.map(item => {
				// Priorizar precios v√°lidos: product.final_price > product.price > item.price > subtotal/quantity
				let price = 0;
				
				if (item.product?.final_price && item.product.final_price > 0) {
					price = item.product.final_price;
				} else if (item.product?.price && item.product.price > 0) {
					price = item.product.price;
				} else if (item.price && item.price > 0) {
					price = item.price;
				} else if (item.subtotal && item.quantity > 0) {
					price = item.subtotal / item.quantity;
				} else {
					console.warn(`‚ö†Ô∏è No se pudo determinar precio para producto ${item.productId}, usando 1.00`);
					price = 1.00; // Precio m√≠nimo como fallback
				}
				
				return {
					product_id: item.productId,
					quantity: item.quantity,
					price: price
				};
			});

			console.log("üõí Items formateados para backend:", JSON.stringify(items, null, 2));

			// Datos de prueba - agregado seller_id y items
			const testData = {
				payment: {
					method: "credit_card" as PaymentMethod,
					card_number: "4111111111111111",
					card_expiry: "12/25",
					card_cvc: "123",
				},
				shippingAddress: {
					name: "Test User",
					street: "Calle de Prueba 123",
					city: "Ciudad de Prueba",
					state: "Estado de Prueba",
					postalCode: "12345",
					country: "Pa√≠s de Prueba",
					phone: "123456789",
				},
				seller_id: sellerId || undefined,
				items: items, // ‚úÖ AGREGAR ITEMS CON PRECIOS V√ÅLIDOS
				// ‚úÖ NUEVO: Incluir c√≥digo de descuento aplicado y su informaci√≥n
				discount_code: appliedDiscount?.discountCode?.code || null,
				discount_info: appliedDiscount || null // ‚úÖ Pasar informaci√≥n completa del descuento
			};

			console.log("üì¶ Datos completos de checkout de prueba:", JSON.stringify(testData, null, 2));
			console.log("üöÄ Enviando checkout al backend...");

			const response = await checkoutService.processCheckout(testData);

			console.log("‚úÖ Respuesta del checkout recibida:", response);

			if (response.status === "success") {
				console.log("üéâ Checkout exitoso, limpiando carrito...");
				clearCart();
				showNotification(
					NotificationType.SUCCESS,
					"¬°Pedido de prueba completado con √©xito!"
				);

				// Mostrar los detalles de la orden por consola
				console.log("üìä Detalles COMPLETOS de la orden:", JSON.stringify(response.data, null, 2));

				// ‚úÖ NUEVO: An√°lisis espec√≠fico de la respuesta
				if (response.data && typeof response.data === 'object') {
					const orderData = response.data as any;
					console.log("üîç AN√ÅLISIS DE LA ORDEN CREADA:");
					console.log("üìä Order ID:", orderData.order_id);
					console.log("üìä Order Number:", orderData.order_number);
					console.log("üìä Total:", orderData.total);
					
					if (orderData.items) {
						console.log("üìä Items en la orden creada:", orderData.items.length);
						orderData.items.forEach((item: any, index: number) => {
							console.log(`üìã Order Item ${index + 1}:`, {
								id: item.id,
								product_id: item.product_id,
								product_name: item.product_name,
								quantity: item.quantity,
								price: item.price
							});
						});
					}
				}

				// Navegar a una p√°gina de confirmaci√≥n o dashboard
				navigate("/orders");
			} else {
				throw new Error(response.message || "Error en el checkout de prueba");
			}
		} catch (error) {
			console.error("‚ùå Error COMPLETO en el checkout de prueba:");
			console.error("üìä Error object:", error);
			console.error("üìä Error stack:", (error as any)?.stack);

			// Usar el extractor de mensajes de error
			const errorMessage = extractErrorMessage(
				error,
				"Error en el checkout de prueba. Por favor, intenta de nuevo m√°s tarde."
			);

			console.error("üìä Error message final:", errorMessage);
			showNotification(NotificationType.ERROR, errorMessage);
		} finally {
			setIsLoading(false);
			console.log("üß™ TestCheckoutButton.handleTestCheckout FINALIZADO");
		}
	};

	return (
		<button
			onClick={handleTestCheckout}
			disabled={isLoading}
			className="px-4 py-2 bg-yellow-500 text-white rounded-md hover:bg-yellow-600 transition-colors flex items-center disabled:opacity-50"
		>
			{isLoading ? (
				<span className="inline-block h-4 w-4 border-2 border-white border-t-transparent rounded-full animate-spin mr-2"></span>
			) : null}
			Prueba r√°pida de checkout
		</button>
	);
};

export default TestCheckoutButton;